.header {
  @include transition(height .4s);
  width: 100%;
  position: absolute;
  overflow: hidden;
  height: 100px;
  z-index: 9997;
  top: 0;
  background: {
    color: $color-gold; }
  border: {
    bottom: 5px solid $color-soft; }

  &.header-home {
    border: {
      bottom: 5px solid $color-dark; } }

  @media #{$small-max} {
    &.active {
      height: 100%; }

    .menu {
      width: 100%;

      .menu-items {
        .item {
          width: 100%;

          a {
            padding: 15px !important;
            width: 100%;
            text-align: center; } } } } }

  .mobile {
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    cursor: pointer;
    font: {
      size: 3rem; }
    padding: 25px;
    color: $color-soft;
    display: none;

    @media #{$small-max} {
      display: block; } }

  .logo {
    width: 160px;
    margin: {
      right: 100px; }
    float: left;
    position: relative;
    height: 100px;

    @media #{$small-max} {
      width: 100%;
      margin: {
        right: 0; } }

    img {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
      max-width: 75%;
      max-height: 30px;

      @media #{$small-max} {
        max-width: 40%; } } }

  .menu {
    float: right;

    .menu-items {
      font: {
        size: 1.6em; }
      text-transform: uppercase;

      .item {
        display: inline-block;

        a {
          @include transition(all .4s);
          padding: 32px 20px;
          color: $color-soft;
          display: block;

          &:hover {
            background: {
              color: darken($color-gold, 5%); } } } } } } }
